             >{{\r\n                                                errors.familyimageTwo[0]\r\n                                            }}</span\r\n                                        >\r\n                                    </label>\r\n                                </div>\r\n                            </div>\r\n                            <div class=\"ven-profile-form-submit\">\r\n                                <button\r\n                                    v-if=\"fedit == false\"\r\n                                    id=\"oprofilebtn\"\r\n                                    :disabled=\"fprofileDisabled\"\r\n                                    type=\"submit\"\r\n                                >\r\n                                    Submit\r\n                                </button>\r\n                                <button\r\n                                    v-else\r\n                                    id=\"fprofilebtn\"\r\n                                    :disabled=\"fprofileDisabled\"\r\n                                    type=\"submit\"\r\n                                >\r\n                                    Update\r\n                                </button>\r\n                            </div>\r\n                        </div>\r\n                        <table class=\"table table-striped\">\r\n                            <thead>\r\n                                <tr>\r\n                                    <th scope=\"col\">#</th>\r\n                                    <th scope=\"col\">Name</th>\r\n                                    <th scope=\"col\">Age</th>\r\n                                    <th scope=\"col\">Gender</th>\r\n                                    <th scope=\"col\">Relation</th>\r\n                                    <th scope=\"col\">Mobile</th>\r\n                                    <th scope=\"col\">Image</th>\r\n                                    <th scope=\"col\">Image</th>\r\n                                    <th scope=\"col\">Actions</th>\r\n                                </tr>\r\n                            </thead>\r\n                            <tbody>\r\n                                <tr\r\n                                    v-for=\"(item, index) in familyList\"\r\n                                    :key=\"index\"\r\n                                >\r\n                                    <th scope=\"row\">{{ index + 1 }}</th>\r\n                                    <td>{{ item.familyname }}</td>\r\n                                    <td>{{ item.familyage }}</td>\r\n                                    <td>{{ item.familygender }}</td>\r\n                                    <td>{{ item.familyrelation }}</td>\r\n                                    <td>{{ item.familymobile }}</td>\r\n                                    <td>{{ item.familyimageOne }}</td>\r\n                                    <td>{{ item.familyimageTwo }}</td>\r\n                                    <td class=\"edit-btn-tab\">\r\n                                        <button\r\n                                            @click=\"\r\n                                                editFamilyForm($event, index)\r\n                                            \"\r\n                                            type=\"button\"\r\n                                        >\r\n                                            <i class=\"fas fa-edit\"></i>\r\n                                        </button>\r\n                                        <button\r\n                                            @click=\"\r\n                                                deleteFamily($event, item.id)\r\n                                            \"\r\n                                            type=\"button\"\r\n                                        >\r\n                                            <i class=\"fas fa-trash\"></i>\r\n                                        </button>\r\n                                    </td>\r\n                                </tr>\r\n                            </tbody>\r\n                        </table>\r\n                    </form>\r\n                </div>\r\n            </div>\r\n        </div>\r\n        <flashMessage :message=\"message\"></flashMessage>\r\n        <div v-if=\"loader\" class=\"form-upload-spinner form-upload-vue\">\r\n            <svg\r\n                width=\"30\"\r\n                height=\"20\"\r\n                viewBox=\"0 0 20 15\"\r\n                fill=\"none\"\r\n                xmlns=\"http://www.w3.org/2000/svg\"\r\n            >\r\n                <path\r\n                    d=\"M3.54979 1.3875C-2.63254 5.39784 5.09979 11.4625 9.74979 13.7875C14.3998 13.0125 22.1498 4.48748 16.7248 1.3875C14.546 0.142315 11.2998 2.1625 9.74979 3.7125C8.19979 2.42084 5.11741 0.370628 3.54979 1.3875Z\"\r\n                    fill=\"#FF5BFF\"\r\n                    stroke=\"#FF5BFF\"\r\n                    stroke-width=\"1.55\"\r\n                />\r\n            </svg>\r\n            <div>\r\n                <svg\r\n                    width=\"70\"\r\n                    height=\"68\"\r\n                    viewBox=\"0 0 49 47\"\r\n                    fill=\"none\"\r\n                    xmlns=\"http://www.w3.org/2000/svg\"\r\n                >\r\n                    <path\r\n                        d=\"M46.83 23.75C46.83 35.3256 37.1128 44.83 24.975 44.83C12.8371 44.83 3.11995 35.3256 3.11995 23.75C3.11995 12.1744 12.8371 2.67 24.975 2.67C37.1128 2.67 46.83 12.1744 46.83 23.75Z\"\r\n                        stroke=\"white\"\r\n                        stroke-width=\"4.34\"\r\n                    />\r\n                </svg>\r\n                <svg\r\n                    width=\"70\"\r\n                    height=\"68\"\r\n                    viewBox=\"0 0 49 47\"\r\n                    fill=\"none\"\r\n                    xmlns=\"http://www.w3.org/2000/svg\"\r\n                >\r\n                    <path\r\n                        d=\"M46.83 23.75C46.83 35.3256 37.1128 44.83 24.975 44.83C12.8371 44.83 3.11995 35.3256 3.11995 23.75C3.11995 12.1744 12.8371 2.67 24.975 2.67C37.1128 2.67 46.83 12.1744 46.83 23.75Z\"\r\n                        stroke=\"white\"\r\n                        stroke-width=\"4.34\"\r\n                    />\r\n                </svg>\r\n            </div>\r\n        </div>\r\n    </div>\r\n</template>\r\n\r\n<script>\r\nimport flashMessage from \"../FlashMessage.vue\";\r\nimport vueValidation from \"../commonComponents/VueValidation.vue\";\r\nimport { helpers } from \"vuelidate/lib/validators\";\r\n\r\nimport {\r\n    required,\r\n    minLength,\r\n    alpha,\r\n    numeric,\r\n    email,\r\n    maxLength,\r\n    url,\r\n} from \"vuelidate/lib/validators\";\r\nexport default {\r\n    components: {\r\n        flashMessage,\r\n        vueValidation,\r\n    },\r\n    props: [\"guestId\", \"exists\", \"cexists\", \"oexists\", \"fexists\"],\r\n    data() {\r\n        return {\r\n            showForm: true,\r\n            loader: false,\r\n            guestProfile: {\r\n                name: null,\r\n                fatherName: null,\r\n                surname: null,\r\n                age: null,\r\n                gender: null,\r\n                relation: null,\r\n                email: null,\r\n                phone: null,\r\n                mobile: null,\r\n                mobile1: null,\r\n                whatsup: null,\r\n                guest_id: this.guestId,\r\n            },\r\n            guestHome: {\r\n                complexName: null,\r\n                floor: null,\r\n                doorNo: null,\r\n                streetNo: null,\r\n                area: null,\r\n                district: null,\r\n                pin: null,\r\n                state: null,\r\n                zone: null,\r\n                country: null,\r\n                reachus: null,\r\n                guest_id: this.guestId,\r\n            },\r\n            guestOffice: {\r\n                officecompanyName: null,\r\n                officecomplexName: null,\r\n                officefloor: null,\r\n                officedoorNo: null,\r\n                officestreetNo: null,\r\n                officearea: null,\r\n                officedistrict: null,\r\n                officepin: null,\r\n                officestate: null,\r\n                officezone: null,\r\n                officecountry: null,\r\n                officereachus: null,\r\n                guest_id: this.guestId,\r\n            },\r\n            guestFamily: {\r\n                familyname: null,\r\n                familyage: null,\r\n                familygender: null,\r\n                familyrelation: null,\r\n                familymobile: null,\r\n                guest_id: this.guestId,\r\n            }, /// create feilds and family memeber and change submit and update for last form to toggle to next link\r\n            sendProfile_imageOne: null,\r\n            guestProfile_logo: null,\r\n            exist: this.exists,\r\n            cexist: this.cexists,\r\n            oexist: this.oexists,\r\n            fexist: this.ofxists,\r\n            profileId: null,\r\n            companyId: null,\r\n            officeId: null,\r\n            guestDetails: null,\r\n            errors: null,\r\n            sendCategory: [],\r\n            catId: [],\r\n            errors: null,\r\n            defaultCategories: null,\r\n            locationMaster: null,\r\n            message: null,\r\n            cprofileDisabled: false,\r\n            pprofileDisabled: false,\r\n            oprofileDisabled: false,\r\n            fprofileDisabled: false,\r\n            pedit: false,\r\n            cedit: false,\r\n            oedit: false,\r\n            fedit: false,\r\n            personalfileOpen: false,\r\n            profilePicPreview: null,\r\n            companyPicPreview: null,\r\n            profileDataSubmitted: false,\r\n            companyDataSubmitted: false,\r\n            familyList: [],\r\n            familyimageOne: null,\r\n            familyimageTwo: null,\r\n            familyimageOnePreview: null,\r\n            familyimageTwoPreview: null,\r\n            famRel: [\r\n                \"Father\",\r\n                \"Mother\",\r\n                \"Wife\",\r\n                \"Husband\",\r\n                \"Son\",\r\n                \"Daughter\",\r\n                \"Son-in-law\",\r\n                \"Daughter-in-law\",\r\n                \"Grand Son\",\r\n                \"Grand Daughter\",\r\n                \"Others\",\r\n            ],\r\n            routeSlug: null,\r\n        };\r\n    },\r\n    validations: {\r\n        guestProfile: {\r\n            name: {\r\n                required,\r\n                minLength: minLength(3),\r\n                maxLength: maxLength(16),\r\n                alpha: helpers.regex(\r\n                    \"alpha\",\r\n                    /^[a-zA-Z-,]+(\\s{0,1}[a-zA-Z-, ])*$/\r\n                ),\r\n            },\r\n            fatherName: {\r\n                required,\r\n                minLength: minLength(3),\r\n                maxLength: maxLength(16),\r\n                alpha: helpers.regex(\r\n                    \"alpha\",\r\n                    /^[a-zA-Z-,]+(\\s{0,1}[a-zA-Z-, ])*$/\r\n                ),\r\n            },\r\n            surname: {\r\n                required,\r\n                minLength: minLength(3),\r\n                alpha: helpers.regex(\r\n                    \"alpha\",\r\n                    /^[a-zA-Z-,]+(\\s{0,1}[a-zA-Z-, ])*$/\r\n                ),\r\n            },\r\n            age: {\r\n                required,\r\n                maxLength: maxLength(3),\r\n                numeric,\r\n            },\r\n            gender: {\r\n                required,\r\n            },\r\n            relation: {\r\n                required,\r\n                minLength: minLength(3),\r\n                alpha: helpers.regex(\r\n                    \"alpha\",\r\n                    /^[a-zA-Z-,]+(\\s{0,1}[a-zA-Z-, ])*$/\r\n                ),\r\n            },\r\n            email: {\r\n                required,\r\n                email,\r\n            },\r\n            phone: {\r\n                required,\r\n                digits_validate: helpers.regex(\"digits_validate\", /^\\d{10}$/),\r\n                numeric,\r\n            },\r\n            mobile: {\r\n                required,\r\n                digits_validate: helpers.regex(\"digits_validate\", /^\\d{10}$/),\r\n                numeric,\r\n            },\r\n            mobile1: {\r\n                required,\r\n                digits_validate: helpers.regex(\"digits_validate\", /^\\d{10}$/),\r\n                numeric,\r\n            },\r\n            whatsup: {\r\n                required,\r\n                digits_validate: helpers.regex(\"digits_validate\", /^\\d{10}$/),\r\n                numeric,\r\n            },\r\n        },\r\n        guestHome: {\r\n            complexName: {\r\n                required,\r\n                minLength: minLength(3),\r\n                alpha: helpers.regex(\r\n                    \"alpha\",\r\n                    /^[a-zA-Z-,]+(\\s{0,1}[a-zA-Z-, ])*$/\r\n                ),\r\n            },\r\n            floor: {\r\n                required,\r\n                minLength: minLength(1),\r\n            },\r\n            doorNo: {\r\n                required,\r\n                minLength: minLength(1),\r\n                numeric,\r\n            },\r\n            streetNo: {\r\n                required,\r\n                minLength: minLength(3),\r\n            },\r\n            area: {\r\n                required,\r\n                minLength: minLength(3),\r\n                alpha: helpers.regex(\r\n                    \"alpha\",\r\n                    /^[a-zA-Z-,]+(\\s{0,1}[a-zA-Z-, ])*$/\r\n                ),\r\n            },\r\n            district: {\r\n                required,\r\n                minLength: minLength(1),\r\n                alpha: helpers.regex(\r\n                    \"alpha\",\r\n                    /^[a-zA-Z-,]+(\\s{0,1}[a-zA-Z-, ])*$/\r\n                ),\r\n            },\r\n            state: {\r\n                required,\r\n                minLength: minLength(3),\r\n                alpha: helpers.regex(\r\n                    \"alpha\",\r\n                    /^[a-zA-Z-,]+(\\s{0,1}[a-zA-Z-, ])*$/\r\n                ),\r\n            },\r\n            pin: {\r\n                required,\r\n                maxLength: maxLength(6),\r\n                numeric,\r\n            },\r\n            zone: {\r\n                required,\r\n                minLength: minLength(3),\r\n                alpha: helpers.regex(\r\n                    \"alpha\",\r\n                    /^[a-zA-Z-,]+(\\s{0,1}[a-zA-Z-, ])*$/\r\n                ),\r\n            },\r\n            country: {\r\n                required,\r\n                minLength: minLength(3),\r\n                alpha: helpers.regex(\r\n                    \"alpha\",\r\n                    /^[a-zA-Z-,]+(\\s{0,1}[a-zA-Z-, ])*$/\r\n                ),\r\n            },\r\n            reachus: { required, url },\r\n        },\r\n        guestOffice: {\r\n            officecompanyName: {\r\n                required,\r\n                minLength: minLength(3),\r\n                alpha_num: helpers.regex(\"alpha_num\", /^[\\w\\-\\s]+$/),\r\n            },\r\n            officecomplexName: {\r\n                required,\r\n                minLength: minLength(3),\r\n                alpha_num: helpers.regex(\"alpha_num\", /^[\\w\\-\\s]+$/),\r\n            },\r\n            officefloor: {\r\n                required,\r\n                minLength: minLength(3),\r\n            },\r\n            officedoorNo: {\r\n                required,\r\n                minLength: minLength(1),\r\n                numeric,\r\n            },\r\n            officestreetNo: {\r\n                required,\r\n                minLength: minLength(3),\r\n            },\r\n            officearea: {\r\n                required,\r\n                minLength: minLength(3),\r\n                alpha: helpers.regex(\r\n                    \"alpha\",\r\n                    /^[a-zA-Z-,]+(\\s{0,1}[a-zA-Z-, ])*$/\r\n                ),\r\n            },\r\n            officedistrict: {\r\n                required,\r\n                minLength: minLength(3),\r\n                alpha: helpers.regex(\r\n                    \"alpha\",\r\n                    /^[a-zA-Z-,]+(\\s{0,1}[a-zA-Z-, ])*$/\r\n                ),\r\n            },\r\n            officestate: {\r\n                required,\r\n                minLength: minLength(3),\r\n                alpha: helpers.regex(\r\n                    \"alpha\",\r\n                    /^[a-zA-Z-,]+(\\s{0,1}[a-zA-Z-, ])*$/\r\n                ),\r\n            },\r\n            officepin: {\r\n                required,\r\n                maxLength: maxLength(6),\r\n                numeric,\r\n            },\r\n            officezone: {\r\n                required,\r\n                minLength: minLength(3),\r\n                alpha: helpers.regex(\r\n                    \"alpha\",\r\n                    /^[a-zA-Z-,]+(\\s{0,1}[a-zA-Z-, ])*$/\r\n                ),\r\n            },\r\n            officecountry: {\r\n                required,\r\n                minLength: minLength(3),\r\n                alpha: helpers.regex(\r\n                    \"alpha\",\r\n                    /^[a-zA-Z-,]+(\\s{0,1}[a-zA-Z-, ])*$/\r\n                ),\r\n            },\r\n            officereachus: { required, url },\r\n        },\r\n        guestFamily: {\r\n            familyname: {\r\n                required,\r\n                minLength: minLength(3),\r\n                alpha: helpers.regex(\r\n                    \"alpha\",\r\n                    /^[a-zA-Z-,]+(\\s{0,1}[a-zA-Z-, ])*$/\r\n                ),\r\n            },\r\n            familyage: {\r\n                required,\r\n                maxLength: maxLength(3),\r\n                numeric,\r\n            },\r\n            familygender: {\r\n                required,\r\n            },\r\n            familyrelation: {\r\n                required,\r\n                minLength: minLength(3),\r\n                alpha: helpers.regex(\r\n                    \"alpha\",\r\n                    /^[a-zA-Z-,]+(\\s{0,1}[a-zA-Z-, ])*$/\r\n                ),\r\n            },\r\n            familymobile: {\r\n                required,\r\n                digits_validate: helpers.regex(\"digits_validate\", /^\\d{10}$/),\r\n                numeric,\r\n            },\r\n        },\r\n    },\r\n    methods: {\r\n        checkImage(url) {\r\n            var request = new XMLHttpRequest();\r\n            request.open(\"GET\", url, true);\r\n            request.send();\r\n            request.onload = function () {\r\n                let status = request.status;\r\n                if (request.status == 200) {\r\n                    //if(statusText == OK)\r\n                    return true;\r\n                } else {\r\n                    return false;\r\n                }\r\n            };\r\n        },\r\n        deleteFamily(e, id) {\r\n            let _this = this;\r\n            this.loader = true;\r\n            axios\r\n                .delete(`/api/guest/profile/${id}`)\r\n                .then(function (response) {\r\n                    _this.loader = false;\r\n                    _this.message = \"Deleted\";\r\n                    console.log(response.data);\r\n                    _this.familyList = response.data;\r\n                    setTimeout(function () {\r\n                        _this.message = null;\r\n                    }, 3000);\r\n                })\r\n                .catch((error) => {\r\n                    _this.loader = false;\r\n                    console.log(error.response.data.errors);\r\n                });\r\n        },\r\n        editFamilyForm(e, id) {\r\n            let _this = this;\r\n            _this.$v.guestFamily.$reset();\r\n            if (id == \"new\") {\r\n                for (const key in this.guestFamily) {\r\n                    _this.guestFamily[key] = null;\r\n                }\r\n\r\n                _this.familyimageOne = null;\r\n                _this.familyimageTwo = null;\r\n                _this.familyimageOnePreview = null;\r\n                _this.familyimageTwoPreview = null;\r\n                $(\"#guestfamilyimagetwo\").val(\"\");\r\n                $(\"#guestfamilyimageone\").val(\"\");\r\n                _this.guestFamily.guest_id = _this.guestId;\r\n                let txt = $(\"#addClose\").text().trim();\r\n                if (txt == \"Add\") {\r\n                    $(\"#addClose\").text(\"Close\");\r\n                }\r\n                if (txt == \"Close\") {\r\n                    $(\"#addClose\").text(\"Add\");\r\n                }\r\n                $(\".guest-details-family-form-cont\").slideToggle(\"linear\");\r\n                _this.fedit = false;\r\n            } else {\r\n                let txt = $(\"#addClose\").text().trim();\r\n                if (txt == \"Add\") {\r\n                    $(\"#addClose\").text(\"Close\");\r\n                }\r\n                if (txt == \"Close\") {\r\n                    $(\"#addClose\").text(\"Add\");\r\n                }\r\n                $(\".guest-details-family-form-cont\").slideDown(\"linear\");\r\n\r\n                _this.guestFamily = { ..._this.familyList[id] };\r\n                _this.fedit = true;\r\n                //console.log(_this.guestFamily);\r\n            }\r\n        },\r\n        uploadImg(e, type) {\r\n            if (type == \"profile\") {\r\n                this.sendProfile_imageOne = e.target.files[0];\r\n                this.profilePicPreview = URL.createObjectURL(\r\n                    this.sendProfile_imageOne\r\n                );\r\n                if (this.sendProfile.imageOne) {\r\n                    delete this.sendProfile.imageOne;\r\n                }\r\n            }\r\n\r\n            if (type == \"company\") {\r\n                this.guestProfile_logo = e.target.files[0];\r\n                this.companyPicPreview = URL.createObjectURL(\r\n                    this.guestProfile_logo\r\n                );\r\n                if (this.guestProfile.imageOne) {\r\n                    delete this.guestProfile.imageOne;\r\n                }\r\n            }\r\n\r\n            if (type == \"familyOne\") {\r\n                this.familyimageOne = e.target.files[0];\r\n                this.familyimageOnePreview = URL.createObjectURL(\r\n                    this.familyimageOne\r\n                );\r\n            }\r\n            if (type == \"familyTwo\") {\r\n                this.familyimageTwo = e.target.files[0];\r\n                this.familyimageTwoPreview = URL.createObjectURL(\r\n                    this.familyimageTwo\r\n                );\r\n            }\r\n        },\r\n        // editForm(e) {\r\n        //     let id = e.currentTarget.parentElement.parentElement.id;\r\n        //     $(`#${id} .ven-profile-form-submit button[type=submit]`).css(\r\n        //         \"display\",\r\n        //         \"block\"\r\n        //     );\r\n        //     $(`#${id} input`).removeAttr(\"disabled\");\r\n        //     $(`#guest-logo`).removeAttr(\"required\");\r\n        //     $(\"#company-logo\").removeAttr(\"required\");\r\n        //     $(`#${id} input`).eq(1).focus();\r\n        //     if (id == \"guest-details\") {\r\n        //         this.pedit = true;\r\n        //         this.pprofileDisabled = false;\r\n        //     }\r\n        //     if (id == \"company-details\") {\r\n        //         this.cedit = true;\r\n        //         this.cprofileDisabled = false;\r\n        //     }\r\n        //     if (id == \"office-details\") {\r\n        //         this.oedit = true;\r\n        //         this.oprofileDisabled = false;\r\n        //     }\r\n        // },\r\n        toggleForm(e, bool) {\r\n            //console.log(bool);\r\n            let id = null;\r\n            if (bool == true) {\r\n                id = e.currentTarget.dataset.id;\r\n            }\r\n            if (bool == false) {\r\n                id = e;\r\n            }\r\n            this.showForm = true;\r\n            this.$nextTick(function () {\r\n                const btns = document.querySelectorAll(\".tab-btn\");\r\n                const articles = document.querySelectorAll(\".content\");\r\n                btns.forEach(function (btn) {\r\n                    btn.classList.remove(\"active\");\r\n                });\r\n                btns.forEach(function (btn) {\r\n                    if (btn.dataset.id == id) {\r\n                        btn.classList.add(\"active\");\r\n                    }\r\n                });\r\n                articles.forEach(function (article) {\r\n                    article.classList.remove(\"active\");\r\n                });\r\n                //console.log(document.getElementById(e.currentTarget.dataset.id));\r\n                document.getElementById(id).classList.add(\"active\");\r\n            });\r\n        },\r\n        submitProfile(e) {\r\n            var _this = this;\r\n            _this.errors = null;\r\n            this.$v.guestHome.$touch();\r\n            if (_this.$v.guestHome.$invalid == true) {\r\n                _this.message =\r\n                    \"Kindly enter the correct inputs to submit the form!\";\r\n                setTimeout(function () {\r\n                    _this.message = null;\r\n                }, 3000);\r\n                return;\r\n            }\r\n            this.pprofileDisabled = true;\r\n            let formData = new FormData();\r\n            let data = JSON.stringify(_this.guestHome);\r\n            formData.append(\"data\", data);\r\n            formData.append(\"type\", \"home\");\r\n            this.loader = true;\r\n            if (this.pedit) {\r\n                form